<template>
  <div class="index-wrap">
    <van-nav-bar
      title="我的卖单"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        shape="round"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <div class="more-search" @click="goPage('/highSearch')">
      高级搜索
      <van-icon name="arrow" />
    </div>
    <van-tabs v-model="active">
      <van-tab title="交易中" name="a">
        <van-panel title="标题" desc="描述信息" status="交易中"></van-panel>
        <van-panel title="标题" desc="描述信息" status="交易中"></van-panel>
        <van-panel title="标题" desc="描述信息" status="交易中"></van-panel>
      </van-tab>
      <van-tab title="纠纷中" name="b">
        <van-panel title="标题" desc="描述信息" status="纠纷中"></van-panel>
        <van-panel title="标题" desc="描述信息" status="纠纷中"></van-panel>
      </van-tab>
      <van-tab title="审核中" name="c">
        <van-panel title="标题" desc="描述信息" status="提现成功"></van-panel>
      </van-tab>
      <van-tab title="已关闭" name="d">
        <van-panel title="标题" desc="描述信息" status="已关闭"></van-panel>
      </van-tab>
      <van-tab title="已完成" name="e">
        <van-panel title="标题" desc="描述信息" status="已完成"></van-panel>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: 'orders',
  data () {
    return {
      value: '',
      active: this.$route.query.type || 'a'
    }
  },
  computed: {
  },
  created () {
  },
  methods: {
    goPage (url) {
      this.$router.push({ path: url })
    },
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    onSearch(val) {
      this.$toast(val)
    },
    onCancel() {
      this.$toast('取消')
    }
  }
}
</script>

<style scope lang='less'>
  .more-search {
    text-align: right;
    font-size: 14px;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 5px 5px 15px 5px;
  }
</style>



